.gazette {
  & figure {
    margin: 0 !important;
    padding: 0 !important;
  }
  & blockquote {
    vertical-align: baseline !important;
    box-shadow: none !important;
  }
  & pre {
    border-radius: 0 !important;
    padding: 0px 5px 0px 5px !important;
    background-color: transparent !important;
    font-size: 1rem !important;
    -webkit-border-radius: 0 !important;
    border: none !important;
  }
  & div.highlight {
    overflow: hidden;
  }
  & .dropcap:first-letter {
    font-size: 3em;
    line-height: 0.9em;
    margin-right: 2px;
    display: block;
    float: left;
  }
  & .scrollCaptionedFigure {
    display: block;
    text-align: center;
    & img {
        max-width: 100% !important;
        height: auto !important;
        margin-top: 3px !important;
      }
      & figcaption {
        font-family: Verdana;
        font-size: 80%;
        & .scrollParagraph {
            margin-top: 0;
            line-height: 1.1em;
          }
      }  
  }
  
  & .scrollCodeBlock {
    overflow: auto !important;
    font-size: 0.8rem !important;
    hyphens: none !important;
    white-space: pre !important;
    break-inside: avoid !important;
    display: block !important;
    margin: 0.5em 0 !important;
    padding: 0.5em !important;
    border-radius: 0 !important;
    position: relative !important;
  }
  & .scrollCodeBlock:hover .scrollCopyButton {
    opacity: 0.5;
  }
  & .scrollCodeBlock:hover .scrollCopyButton:hover {
    opacity: 0.8;
  }
  & .scrollCodeBlock:hover .scrollCopyButton:active {
    opacity: 1;
  }
  & .scrollCopyButton {
    position: absolute;
    top: 2px;
    right: 2px;
    font-size: 14px;
    cursor: pointer;
    opacity: 0;
  }
  & .scrollCopyButton::after {
    content: "[ ]";
  }
  & .scrollCopiedButton::after {
    content: "[✓]";
  }
  & div,
  & span,
  & p,
  & ol,
  & ul,
  & li,
  & table,
  & figure {
    margin: 0 !important;
    padding: 0 !important;
    border: 0 !important;
    vertical-align: baseline !important;
    border-spacing: 0 !important;
  }
  & ol,
  & ul {
    padding-left: 1em !important;
  }
  & li {
    margin-top: 0.4em !important;
    line-height: 140% !important;
  }
  & a {
    text-decoration-color: transparent;
    color: $color-link;
    &:hover {
        text-decoration-color: initial;
        color: $color-accent-2;
        background-color: $color-accent-1;
      }
    &:visited {
        color: $color-link;
      }
    }

  & sup,
  & sub {
    vertical-align: baseline !important;
    position: relative !important;
    top: -0.6em !important;
  }
  & sub {
    top: 0.6em !important;
  }
  & p {
    margin-top: 0.4em !important;
    line-height: 1.4em !important;
  }
  & .scrollQuote {
    break-inside: avoid !important;
    display: block !important;
    margin: 0.5em 0 !important;
    padding: 0.5em !important;
    background: rgba(204, 204, 204, 0.5) !important;
    white-space: pre-line !important;
    border-left: 0.5em solid rgba(204, 204, 204, 0.8) !important;
  }
  & code {
    font-size: 0.9rem !important;
    background-color: rgba(204, 204, 204, 0.5) !important;
    padding: 2px 4px !important;
    border-radius: 4px !important;
  }
  & .scrollParagraph {
    text-align: justify !important;
  }
  & .scrollColumns {
    column-count: auto !important;
    column-fill: balance !important;
    column-width: 35ch !important;
    column-gap: 20px !important;
    padding-left: 20px !important;
    padding-right: 20px !important;
    margin: auto !important;
  }
  & .scrollSnippetContainer {
    padding: 1ch 0 !important;
    break-inside: avoid !important;
    text-align: justify !important;
  }
  & h1 {
    font-size: 2.3rem !important;
    margin: 10px 0 !important;
  }
  & h2 {
    font-size: 1.3rem !important;
    margin: 10px 0 !important;
  }
  & h3 {
    font-size: 1.1rem !important;
    margin: 10px 0 !important;
    border-bottom: none !important;
  }
  & h4 {
    margin: 10px 0 !important;
    font-size: 13px !important;
    font-weight: normal !important;
  }
  & h1.scrollTitle {
    font-size: 2.3rem !important;
    text-align: center !important;
    margin-bottom: 0.25em !important;
    margin-top: 0 !important;
    font-size: 28px !important;
  }
  & h1.scrollTitle a {
    color: #000 !important;
  }
  & .scrollDateline {
    font-style: italic !important;
    font-size: 80% !important;
  }
  & .scrollSection {
    break-inside: avoid !important;
  }
  & .scrollSection h1 {
    text-align: center !important;
  }
  & .scrollSection h2 {
    text-align: center !important;
  }
  & .scrollSection h3 {
    text-align: center !important;
    border-bottom: none !important;
  }
  & .scrollSection h4 {
    text-align: center !important;
    color: rgb(125, 125, 125) !important;
  }
  & span.date {
    color: rgb(125, 125, 125) !important;
    font-style: italic !important;
  }

  & h4.scrollQuestion {
    text-align: left;
  }
  & .scrollNoteLink {
    opacity: 0.4;
  }
  & .scrollHoverNote {
    text-decoration: underline dashed 1px rgba(0, 0, 0, 0.1);
    cursor: default;
  }
  & .scrollCodeBlock {
    border-left: 0.5em solid rgba(204, 204, 204, 0.8) !important;
  }
  & .scrollTable {
    table-layout: fixed;
    margin: 0.5em 0;
    overflow: hidden;
    font-size: 80%;
    width: 100%;
    hyphens: none;
    border: 1px solid rgba(224, 224, 224, 0.8);
  }
  & .scrollTable td,
  & .scrollTable th {
    padding: 3px;
    overflow: hidden;
  }
  & .scrollTable th {
    border-bottom: 2px solid rgba(0, 0, 0, 0.6);
    text-align: left;
  }
  & .scrollTable tr:nth-child(even) {
    background: rgba(224, 224, 224, 0.6);
  }
  & .scrollByLine {
    font-size: 12px;
    font-style: italic;
    margin: 4px 0;
    text-align: center;
  }
  & .scrollViewSource {
    text-align: center;
    font-size: 80%;
    margin: 0;
    margin-top: 0.4em;
    line-height: 1.4em !important;
    margin-bottom: 1em;
  }
  & .scrollViewSource a {
    color: #000;
  }
  & .scrollContinueReadingLink {
    display: block;
    text-align: center;
  }
  & .scrollDashboard {
     width: 100%;
    font-size: 30px;
    text-align: center;
    font-weight: bold;
    break-inside: avoid;
    margin-top: 8px;
    margin-bottom: 8px;
  }
  & .scrollDashboard td {
    width: 33.3%;
    border: 1px solid #e8e8e8;
  }
  & .scrollDashboard span {
    font-size: 20p;
    display: block;
  }
  & .scrollChat span {
    font-family: Verdana;
    margin-top: 5px;
    padding: 5px 20px;
    border-radius: 15px;
    display: inline-block;
  }
  & .scrollChatLeft span {
    background: rgba(204, 204, 204, 0.5);
  }
  & .scrollChatRight span {
    color: white;
    background: rgb(0, 132, 255);
  }
  & .scrollFootNoteUsageLink {
    color: rgba(204, 204, 204, 0.5);
  }
  & .scrollFootNoteUsageLink:hover {
    color: #333;
    text-decoration: none;
  }
  & .scrollYouTubeHolder {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 56.25%;
  }
  & .scrollYouTubeEmbed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  & .pageHeader {
    position: absolute;
    top: 4px;
    right: 0;
    left: 0;
  }
  & .pageHeader svg {
    width: 30px;
    height: 30px;
    fill: rgba(204, 204, 204, 0.8);
  }
  & .pageHeader svg:hover {
    fill: #333;
  }
  & .pageHeader a {
    color: rgba(204, 204, 204, 0.8);
    position: absolute;
    font-size: 30px;
    line-height: 27px;
    text-decoration: none;
  }
  & .pageHeader a:hover {
    color: #333;
  }
  & .pageFooter {
    margin-top: 8px;
    padding-top: 8px;
    text-align: center;
  }
  & .pageFooter svg {
    width: 30px;
    height: 30px;
    fill: rgba(204,204,204, .5);
    padding: 0 7px;
  }
  & .pageFooter svg:hover {
    fill: #333;
  }
  & .pageFooterScrollLink {
    display: block;
    font-family: Verdana;
    font-weight: 100;
    margin: .5em;
    color: rgba(204,204,204,.5);
  }
  & .pageFooterScrollLink:hover {
    color: #333;
    text-decoration: none;
  }
}
