@font-face {
  font-style: normal;
  font-family: "Fira Code";
  font-display: swap;
  src: url("/fonts/firacode/FiraCode-Regular.woff2") format("woff2");
}

@font-face {
  font-style: normal;
  font-family: "Vollkorn";
  font-display: swap;
  src: url("/fonts/vollkorn/Vollkorn-Regular.woff2") format("woff2");
}

@font-face {
  font-style: normal;
  font-weight: bold;
  font-family: "Vollkorn";
  font-display: swap;
  src: url("/fonts/vollkorn/Vollkorn-Bold.woff2") format("woff2");
}

// Italic variant for Vollkorn
@font-face {
  font-style: italic;
  font-family: "Vollkorn";
  font-display: swap;
  src: url("/fonts/vollkorn/Vollkorn-Italic.woff2") format("woff2"); // Assuming you have the italic variant named as "Vollkorn-Italic.woff2"
}

// Bold and Italic variant for Vollkorn (if you have one)
@font-face {
  font-style: italic;
  font-weight: bold;
  font-family: "Vollkorn";
  font-display: swap;
  src: url("/fonts/vollkorn/Vollkorn-BoldItalic.woff2") format("woff2"); // Assuming you have the bold italic variant named as "Vollkorn-BoldItalic.woff2"
}
