.portfolio {
  .draw-title {
    text-align: center;
    font-size: 2em;
    font-weight: bold;
    margin-bottom: 1em;
    margin-left: 3em;
  }

  .gallery {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 10px;
  }

  .card {
    background: #f9f9f9;
    margin: 10px;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
    width: 200px;
    text-align: center;
    transition: 0.3s;
  }

  .card:hover {
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
  }

  .card img {
    width: 100%;
    height: 150px; /* adjust this to suit your needs */
    object-fit: cover;
    object-position: center;
  }
}

.draw {
  .title {
    font-family: sans-serif;
    font-size: 0.75em;
    font-weight: bold;
    font-style: italic;
  }
  .year {
    font-size: 0.75em;
    font-family: sans-serif;
    color: #5e5e5e;
  }
  .medium {
    font-family: sans-serif;
    color: #5e5e5e;
    font-size: 0.7em;
  }
  .plaque {
    background-image: url(/drawings/resources/paper-texture.jpg);
    border: #5e5e5e;
    padding-left: 16px;
    padding-top: 12px;
    padding-bottom: 19px;
    // max-width: 600px;
    max-width: 45%;
    margin-top: 10px;
    box-shadow: 2px 2px 4px 1px rgba(0, 0, 0, 0.2);
    // margin-left: auto;
  }
  canvas {
    box-shadow: 2px 2px 4px 1px rgba(0, 0, 0, 0.2);
    width: 100%;
  }
  #canvas {
    margin-top: 2em;
  }
}
