// $base-style
h1,
.h1 {
  display: block;
  margin-top: 3rem;
  margin-bottom: 1rem;
  color: black;
  letter-spacing: $letter-spacing-h1;
  font-weight: 700;
  font-style: normal;
  font-size: $font-size-h1;

  @include antialias();
}
h2,
.h2 {
  position: relative;
  display: block;
  margin-top: 2rem;
  margin-bottom: 0.5rem;
  color: $color-accent-2;
  text-transform: none;
  letter-spacing: $letter-spacing-h2;
  font-weight: bold;
  font-size: $font-size-h2;
  border-bottom: 1px solid $color-border;
}
h3 {
  color: $color-accent-2;
  text-decoration: none;
  font-weight: bold;
  font-size: $font-size-h3;
  letter-spacing: $letter-spacing-h3;
  border-bottom: 1px dotted $color-border;
}
h4 h5 h6 {
  display: inline;
  text-decoration: none;
  color: var(--color-accent-3);
  font-weight: bold;
  font-size: $font-size-h4;
  letter-spacing: $letter-spacing-h4;
}
h3 h4 h5 h6 {
  margin-top: 0.9rem;
  margin-bottom: 0.5rem;
}
hr {
  border: 0.5px dashed $color-accent-3;
  opacity: 0.5;
  margin: 0;
  margin-top: 20px;
  margin-bottom: 20px;
}
strong {
  font-weight: bold;
}
em cite {
  font-style: italic;
}
sup sub {
  position: relative;
  vertical-align: baseline;
  font-size: 0.75em;
  line-height: 0;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.2em;
}
small {
  font-size: 0.85em;
}
acronym abbr {
  border-bottom: 1px dotted;
}
ul ol dl {
  line-height: $line-height;
}
ul ul,
ol ul,
ul ol,
ol ol {
  margin-top: 0;
  margin-bottom: 0;
}
ol {
  list-style: decimal;
}
dt {
  font-weight: bold;
}
table {
  width: 100%;
  border-collapse: collapse;
  text-align: left;
  font-size: $font-size - 0.1rem;
  overflow: auto;
  display: block;
}
th {
  padding: 8px;
  border-bottom: 1px dashed $color-border;
  color: $color-accent-2;
  font-weight: bold;
  font-size: $font-size - 0.05rem;
}
td {
  padding: 0 8px;
  border-bottom: none;
}

.dataframe {

  border: none;

  & tbody tr th:only-of-type {
    vertical-align: middle;
    background-color: var(--color-accent-1);
    font-size: $font-code-size - 0.1;
    color: var(--color-link);
  }
  
  & tbody {
    & tr th {
      vertical-align: top;
    }
    font-family: $font-family-mono;
    font-size: $font-code-size - 0.1;
  }
  
  & thead th {
    text-align: right;
    background-color: var(--color-accent-1);
    font-size: $font-code-size - 0.1;
    color: var(--color-link);
  }
  

}
