@import "variables";
@import "colors/white";
@import "util";
@import "mixins";
@import "extend";
@import "fonts";
@import "gazette";
/* HUGO_IMPORT_START ../../static/lib/fonts/Inter/inter.css HUGO_IMPORT_END */
// global-reset()

:root {
  --font-family-body: #{$font-family-body};
  --font-family-mono: #{$font-family-mono};
  --color-accent-1: #{$color-accent-1};
  --color-accent-3: #{$color-accent-3};
  --color-link: #{$color-link};
}

*,
*:before,
*:after {
  box-sizing: border-box;
}
html {
  margin: 0;
  padding: 0;
  height: 100%;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}
body {
  margin: 0;
  height: 100%;
  background-color: $color-background;
  color: $color-text;
  font-display: swap;
  font-weight: 400;
  font-size: $font-size;
  font-family: var(--font-family-body);
  line-height: $line-height;
  text-rendering: geometricPrecision;
  flex: 1;

  @include antialias();

  @extend $base-style !optional;
}
.content {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  overflow-wrap: break-word;

  p {
    @include hyphens(auto);
  }
  code {
    @include hyphens(manual);
  }

  a {
    color: var(--color-link);
    text-decoration: underline !important;
    // color: $color-text;
    // background-color: lighten($color-accent-1, 50%);
    // padding: 0.2rem;
    // border-radius: 0.3rem;
    // // color: lighten($color-accent-1, 50%);
    // text-decoration: none;

    // @include underline(5px, white);

    &:hover {
      // background-image: linear-gradient(transparent, transparent 4px, $color-link 4px, $color-link);
      @include underline(5px, $color-link, $color-accent-1);
      // @include hover($color-accent-1, $color-background);
    }
    &[href^="#org-coderef"] {
      text-decoration: underline wavy $color-accent-1 !important;
      background-color: white !important;

      &:hover {
        color: $color-text;
        background-color: lighten($color-accent-1, 50%) !important;
      }
    }
  }
  a.icon {
    background: none;

    &:hover {
      color: var(--color-link);
    }
  }
  h1 a,
  .h1 a,
  h2 a,
  h3 a,
  h4 a,
  h5 a,
  h6 a {
    background: none;
    color: inherit;
    text-decoration: none;
  }
  h1 a:hover,
  .h1 a:hover,
  h2 a:hover,
  h3 a:hover,
  h4 a:hover,
  h5 a:hover,
  h6 a:hover {
    @include underline(6px, $color-accent-1, $color-link);
  }
  h6 {
    a {
      background: none;
      color: inherit;
      text-decoration: none;
    }
  }
  h6 {
    a:hover {
      @include underline(6px, $color-accent-1, $color-link);
    }
  }
}

.page-age {
  color: #bbb;
  font-size: 0.9rem;
}

ul {
  &.post-list {
    -moz-column-count: 2;
    -moz-column-gap: 20px;
    -webkit-column-count: 2;
    -webkit-column-gap: 20px;
    column-count: 2;
    column-gap: 20px;
  }
}

@media (min-width: 540px) {
  .image-wrap {
    flex-direction: row;
    margin-bottom: 2rem;

    .image-block {
      flex: 1 0 35%;
      margin-right: 2rem;
    }
    p {
      flex: 1 0 65%;
    }
  }
}
.max-width {
  max-width: $page-width;
}
@media (max-width: 480px) {
  // smaller margins at smaller screen widths
  .px3 {
    padding-right: 1rem;
    padding-left: 1rem;
  }
  .my4 {
    margin-top: 2rem;
    margin-bottom: 2rem;
  }
}

@media (min-width: 480px) {
  p {
    text-align: justify;
  }
}

