blockquote {
  background: $color-accent-1;
  font-style: italic;
  border: solid 1px $color-accent-1;
  padding: 20px 30px 20px 30px;
  margin: 50px auto;
  width: 90%;
  box-shadow: 13px 13px 0 0 darken($color-accent-1, 7%), 15px 15px 0 0 darken($color-accent-1, 2%), 15px 11px 0 0 darken($color-accent-1, 14%),
    11px 15px 0 0 $color-accent-1;
}

pre {
  overflow-x: auto;
  padding: 15px 15px 10px 15px;
  border: 1px dashed #93A1A1;
  border-radius: 2px;
  -webkit-border-radius: 2px;
  font-size: $font-code-size;
  font-family: var(--font-family-mono);
  line-height: $line-height;
  position: relative;
  background-color: var(--color-accent-1) !important;

  .code-copy-btn {
    position: absolute;
    top: 0;
    right: 0;
    border: 0;
    border-radius: 0 2px;
    padding: 0;
    font-family: var(--font-family-mono);
    font-weight: 800;
    font-size: $font-code-size;
    line-height: 1.7;
    color: #fff;
    background-color: #8c8c8c;
    min-width: 60px;
    text-align: center;
    cursor: pointer;
    letter-spacing: 0em;
  }

  .code-copy-btn:hover {
    background-color: #666;
    color: var(--color-accent-1);
  }

  code {
    display: block;
    padding: 0;
    border: none;
    background-color: var(--color-accent-1);
    font-size: $font-code-size;
  }
}

code {
  font-family: var(--font-family-mono);
  padding: 0 5px;
  background-color: var(--color-accent-1);
  border: 1px dotted rgb(200, 200, 200);
  border-radius: 0.25rem;
  -webkit-border-radius: 0.25rem;
  font-size: $font-code-size;
}

.footnotes {
  font-size: 0.9rem !important;
  line-height: 1.0;

  code {
    font-size: 0.8rem;
  }
}

.footnote-ref {
  color: #888 !important;
  &::before {
    content: "[";
    color: #bbb;
  }
  &::after {
    content: "]";
    color: #bbb;
  }
}

.highlight {
  & > div {
    border-radius: 2px;
    -webkit-border-radius: 2px;
  }

  pre {
    // border: none;
    background-color: rgb(247, 247, 247);
  }

  table {
    pre {
      margin-top: 0;
    }

    td:first-child {
      pre {
        padding-right: 0;
      }
    }

    td:last-child {
      pre {
        padding-left: 0;
      }
    }
  }
}

span.focus {
  cursor: pointer !important;
  text-decoration-style: wavy;
  text-decoration-color: $color-text !important;
  background-color: $color-accent-1;
  // background-color: $color-accent-2;

  &:hover {
    background-color: darken($color-accent-1, 20%);
  }
  
  &::after {
    content: "\01F50D";
    vertical-align: super;
    font-size: 0.5rem;
    color: $color-accent-3; 
  }
}

input#search-query {
  font-family: var(--font-family-body);
}

mark{
  background: lighten($color-link, 55%);
  border-radius: 5px;
  color: var(--color-accent-1);
}

.MathJax {
  font-size: 1.1rem !important;
}

.backlink-count {
  background: $color-quote;
  color: lighten($color-link, 55%);
  font-weight: bold;
  border-radius: 7px;
  padding: 0px 3px 2px 3px;
  // text-align:justify;
  margin-right: 4px;
  font-size: 0.75rem;
}

figcaption {
  position: relative;
  display: block;
  margin-top: .5em;
  color: $color-meta;
  text-align: center;
  font-size: .8em;
}

.sidenote {
  float: left;
  clear: left;
  margin-left: -25vw;
  text-align: left;
  // top: -0.1rem;
  width: 20vw;
  margin-top: 0.5rem;
  padding-top: 0.25rem;
  position: relative;
  font-size: 0.9rem;
  border-top: 1px dotted $color-accent-3; /* Adjust color as necessary */
}
.sidenote-colour {
  color: $color-meta;
}



